<template>
    <div>
        <div class="slide">
        <!--  <ul>
                <li>
                    <img src="../../assets/img/mi0222.jpg" alt="">
                </li>
            </ul> -->
            <div class="swiper-container slidedetailsContainer">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <img src="../../assets/img/mi0222.jpg" alt="">
                    </div>
                    <div class="swiper-slide">
                        <img src="../../assets/img/mi0222.jpg" alt="">
                    </div>
                    <div class="swiper-slide">
                        <img src="../../assets/img/mi0222.jpg" alt="">
                    </div>
                    <div class="swiper-slide">
                        <img src="../../assets/img/mi0222.jpg" alt="">
                    </div>
                </div>
            </div>
            <div class="prev"  @click="slidePrev">左按钮</div>
            <div class="next" @click="slideNext">右按钮</div>
            <ol class="clearfix">
                <li  @click="slideTo(0)" :class="[index == 0?'con':'']"></li>
                <li  @click="slideTo(1)" :class="[index == 1?'con':'']"></li>
                <li  @click="slideTo(2)" :class="[index == 2?'con':'']"></li>
                <li  @click="slideTo(3)" :class="[index == 3?'con':'']"></li>
            </ol>
        </div>
    </div>
</template>

<script>
import Swiper from "swiper";
    export default {
		data(){
			return {
				swiperobj:null,
				index:0,
			}
		},
		methods:{
			// 上一页功能
			slidePrev(){
				// swiper 对象内方法slidePrev()
				this.swiperobj.slidePrev();

			},
			// 下一页功能
			slideNext(){
				// swiper 对象内方法slideNext()
				this.swiperobj.slideNext();
			},
			// 指示点功能
			slideTo(index){
				this.swiperobj.slideTo(index);
			}
		},
		mounted(){
			let _this = this;
			this.swiperobj = new Swiper('.slidedetailsContainer',{
				// loop:true,
				on: {
					// slideChangeTransitionEnd轮播图切换时触发方法
					slideChangeTransitionEnd: function(){
						_this.index = this.activeIndex;
					},
				},
			});
		}
    }
</script>

<style lang="scss" scoped>
.slidedetailsContainer{
    z-index: -1;
}
    .slide{
        margin-top:47px;
        height: 530px;
        width: 560px;
        position: relative;
        
        .prev,
        .next {
            width: 41px;
            height: 69px;
            background: url(../../assets/img/icon-slides.png) no-repeat -83px 0px;
            line-height: 300px;
            overflow: hidden;
        }

        .next{
            background-position: -124px 0px;
            position: absolute;
            right: 0px;
            top: 50%;
            margin-top: -34px;
        }

        .prev {
            position: absolute;
            left: 0px;
            top: 50%;
            margin-top: -34px;
        }

        .next:hover {
            background-position: -42px 0px;
        }

        .prev:hover {
            background-position: 0px 0px;
        }
        ol{
            position:absolute;
            left:0px;
            right: 0px;
            bottom: 0px;
            margin: 0 auto;
            width: 240px;
            li{
                width: 50px;
                height: 2px;
                background: #CCCCCC;
                margin: 0px 5px;
                float: left;
                line-height: 50px;
                overflow: hidden;
                &.con{
                    background: #A3A3A3;
                }
            }
        }
    }
</style>